import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { nanoid } from 'nanoid';
import s from './Input.module.css';

class Input extends Component {
  state = {
    name: '',
    number: '',
  };

  Contact = event => {
    const { name, value } = event.currentTarget;
    this.setState({ [name]: value });
  };

  reset = () => {
    this.setState({ name: '', number: '' });
  };

  AddNewContact = event => {
    if (
      this.props.contacts.find(
        item => item.name.toLowerCase() === this.state.name.toLowerCase(),
      )
    ) {
      return window.alert(`${this.state.name} is alredy in contacts`, 5000);
    } else {
      this.props.addContact(this.RenderContact());
      this.reset();
    }
  };

  RenderContact = () => {
    const { name, number } = this.state;
    return { id: nanoid(), name: name, number: number };
  };

  render() {
    return (
      <form className={s.form}>
        <h3 className={s.title}>Name</h3>
        <label>
          <input
            className={s.input}
            type="text"
            name="name"
            value={this.state.name}
            pattern="^[a-zA-Zа-яА-Я]+(([' -][a-zA-Zа-яА-Я ])?[a-zA-Zа-яА-Я]*)*$"
            title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
            required
            onChange={this.Contact}
            placeholder="Enter name"
          />
          <h3 className={s.title}>Number</h3>
          <input
            className={s.input}
            type="tel"
            name="number"
            value={this.state.number}
            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
            title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
            required
            onChange={this.Contact}
            placeholder="Enter phone number"
          />
        </label>
        <button className={s.button} type="button" onClick={this.AddNewContact}>
          Add contact
        </button>
      </form>
    );
  }
}

Input.propTypes = {
  name: PropTypes.string,
  number: PropTypes.number,
};

export default Input;
